import keyMirror from 'keymirror';

const constants = keyMirror({
  SET_DATABASE: null,
  SET_NAMESPACE: null,
  SIDEBAR_TOGGLE: null,
  TABLE_SET_NAMESPACES_COLUMNS: null,
  SETTINGS_DATABASE_RESET: null,
  SETTINGS_NAMESPACE_RESET: null,
});

export default {
  ...constants,
};



