import { ADD_TOAST_MESSAGE } from 'vuex-toast';
import resultGetter from '@/utils/resultGetter';
import types from './mutation-types';

export const resetSelectedNamespace = (context) => {
  const { commit } = context;

  commit(types.SETTINGS_NAMESPACE_RESET);
};

export const selectDatabase = (context, name) => {
  const { commit } = context;
  const payload = {
    name,
  };

  commit(types.SET_DATABASE, payload);
};

export const selectNamespace = (context, name) => {
  const { commit } = context;
  const payload = {
    name,
  };

  commit(types.SET_NAMESPACE, payload);
};

export const sidebarToggle = (context) => {
  const { commit } = context;

  commit(types.SIDEBAR_TOGGLE);
};

export const throwError = (context, error) => {
  const { dispatch } = context;

  return dispatch(
    ADD_TOAST_MESSAGE,
    { text: resultGetter.errorDescription(error), type: 'danger' },
    { root: true },
  );
};

export const showSuccessMessage = (context, text) => {
  const { dispatch } = context;

  return dispatch(ADD_TOAST_MESSAGE, { text, type: 'success' }, { root: true });
};

export const setSelectedFieldsForNamespace = (context, payload) => {
  const { commit } = context;

  return commit(types.TABLE_SET_NAMESPACES_COLUMNS, payload);
};

export default {
  resetSelectedNamespace,
  selectDatabase,
  selectNamespace,
  sidebarToggle,
  throwError,
  showSuccessMessage,
  setSelectedFieldsForNamespace,
};



