<template>
  <default-layout>
    <div slot="page">
      <preloader class="preloader" :isLoading='isLoading' />
      <b-alert show v-if="!namespace">Select a namespace</b-alert>
      <h3>{{namespace}}</h3>
      <data-table
        v-if="namespace && indexesSize"
        class="mt-5"
        isWithActions
        isRemoveAllowed
        :title='"Indexes"'
        :items="indexes"
        :columns="selectedFields"
        :selectedFields="selectedFields"
        :onItemDelete="onIndexDeleteQuestion"
      />

      <div v-if="namespaceOneExists" class="d-flex flex-wrap align-content-stretch mt-5">
        <b-container fluid>
          <b-row>
            <b-col><b-input class="w-100" placeholder="Name" v-model="newIndex.name" /></b-col>
            <b-col><b-input placeholder="Json path" v-model="newIndex.jsonPath" /></b-col>
            <b-col>
              <b-form-select id="field_type" :value="null" v-model="newIndex.fieldType"
                :options="['int', 'int64', 'double', 'string', 'bool', 'composite']">
                <template slot="first">
                  <option :value="''" disabled>Field Type</option>
                </template>
              </b-form-select>
            </b-col>
            <b-col>
              <b-form-select id="index_type" :value="null" v-model="newIndex.indexType"
                :options="['hash', 'tree', 'text', '-']">
                <template slot="first">
                  <option :value="''" disabled>Index Type</option>
                </template>
              </b-form-select>
            </b-col>
            <b-col cols="5" class="text-center">
              <label class="mt-2"><input type="checkbox" v-model="newIndex.isPk">Pk</label>
              <label class="mt-2 ml-4"><input type="checkbox" v-model="newIndex.isArray">Array</label>
              <label class="mt-2 ml-4"><input type="checkbox" v-model="newIndex.isDense">Dense</label>
              <label class="mt-2 ml-4"><input type="checkbox" v-model="newIndex.isAppendable">Appendable</label>
            </b-col>
            <b-col>
              <b-form-select id="collate_mode" :value="null" v-model="newIndex.collateMode"
                :options="['none', 'ascii', 'utf8', 'numeric']">
                <template slot="first">
                  <option :value="''" disabled>Collate</option>
                </template>
              </b-form-select>
            </b-col>
            <b-col>
              <b-button class="float-right" variant="primary" @click.prevent="onIndexAddQuestion(newIndex)">Create</b-button>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import Preloader from '@/components/Preloader';
import DefaultLayout from '@/layouts/DefaultLayout';
import DataTable from '@/components/DataTable';

export default {
  name: 'NamespacesPage',

  components: {
    DefaultLayout,
    Preloader,
    DataTable,
  },

  data() {
    const indexTemplate = {
      name: '',
      jsonPath: '',
      fieldType: '',
      indexType: '',
      collateMode: '',
      isPk: false,
      isArray: false,
      isDense: false,
      isAppendable: false,
    };

    return {
      newIndex: {
        ...indexTemplate,
      },

      newIndexTemplate: {
        ...indexTemplate,
      },
    };
  },

  computed: {
    ...mapState({
      namespace: state => state.settings.namespace,
      namespaceOne: state => state.namespaces.one,
      isLoading: state => state.namespaces.isLoading,
    }),

    ...mapGetters({
      indexes: 'namespaces/indexes',
      fields: 'namespaces/indexFields',
    }),

    namespaceOneExists() {
      return !_.isEmpty(this.namespaceOne);
    },

    indexesSize() {
      return _.size(this.indexes);
    },

    selectedFields() {
      return _.cloneDeep(this.fields);
    },
  },

  methods: {
    onIndexDeleteQuestion(index) {
      const { name } = index;

      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the index:',
        strongMessage: name,
        event: 'onIndexDelete',
        eventParams: name,
      });
    },

    onIndexAddQuestion(newIndex) {
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will create a new index:',
        strongMessage: newIndex,
        event: 'onIndexCreate',
        eventParams: newIndex,
      });
    },
  },

  watch: {
    namespaceOne(newNamespaceOne) {
      if (!_.isEqual(this.namespaceOne, newNamespaceOne)) {
        this.newIndex = _.copyDeep(this.newIndexTemplate);
      }
    },
  },
};
</script>

<style scoped>
.datatable {
  margin-top: 30px;
}
</style>



