<template>
  <b-modal centered :title="header" v-model="isShowed" @ok="onConfirm">
    <center>{{message}}</center>
    <br/>
    <center v-if='isApprovalType'><strong>{{strongMessage}}</strong></center>

    <b-form-input v-if='isCreatorType' v-model="value" size="sm" type="text" placeholder="Enter a name"></b-form-input>
  </b-modal>
</template>

<script>
import ModalEventBus from './ModalEventBus';

export default {
  name: 'Modal',

  created() {
    ModalEventBus.$on('show', this.show);
  },

  destroy() {
    ModalEventBus.$off('show', this.show);
  },

  data() {
    return {
      type: 'approval',

      header: '',
      message: '',
      strongMessage: '',
      event: null,
      eventParams: {},

      value: '',

      isShowed: false,
    };
  },

  computed: {
    isApprovalType() {
      return this.type === 'approval';
    },

    isCreatorType() {
      return this.type === 'creator';
    },
  },

  methods: {
    show(opts = {}) {
      const { type, header, message, strongMessage, event, eventParams } = opts;

      this.type = type || 'approval';

      this.header = header || '';
      this.message = message || '';
      this.strongMessage = strongMessage || '';
      this.event = event || '';
      this.eventParams = eventParams || {};

      this.isShowed = true;
    },

    onConfirm(e) {
      e.preventDefault();

      if (this.event) {
        if (this.isApprovalType) this.$emit(this.event, this.eventParams);

        if (this.isCreatorType) {
          this.$emit(this.event, {
            ...this.eventParams,
            value: this.value,
          });
        }
      }

      this.value = '';
      this.isShowed = false;
    },
  },
};
</script>



