import _ from 'lodash';

export default {
  items(data) {
    return _.get(data, 'items', []);
  },

  totalItems(data) {
    const total = _.get(data, 'total_items', 0);

    // temp avoid api bug
    return total < 0 ? -total : total;
  },

  fields(items) {
    if (_.isEmpty(items)) {
      return [];
    }

    const allFields = items.reduce((currFieldsSet, item) => {
      const itemFieldsSet = _.keys(item);

      return [...currFieldsSet, ...itemFieldsSet];
    }, _.keys(items[0]));

    return [...new Set(allFields)];
  },

  itemsComplexPropsStringified(items) {
    const newItems = JSON.parse(JSON.stringify(items));

    if (!(newItems instanceof Array)) {
      return [];
    }

    return newItems.map((item) => {
      const props = Object.keys(item);

      return props.reduce((itemObj, prop) => {
        const newItemObj = itemObj;

        newItemObj[prop] =
          typeof item[prop] === 'object'
            ? JSON.stringify(item[prop])
            : (newItemObj[prop] = item[prop]);

        return newItemObj;
      }, {});
    });
  },

  errorDescription(error) {
    return (
      _.get(error, 'response.data.description', '') ||
      _.get(error, 'response', '') ||
      'Error is occurred'
    );
  },
};



