<template>
  <default-layout>
    <div slot="page">
      <preloader class="preloader" :isLoading='isLoading' />
      <h3>Queries</h3>

      <b-card class="text-center mt-4">
        <b-form-textarea
          v-model="customQuery"
          @input="changeQuery"
          placeholder="Write a custom query"
          :rows="3"
          :max-rows="6" />
        <b-btn class="mt-3" size="md" variant="success" @click.stop="onCustomQueryExecute">Execute</b-btn>
      </b-card>

      <b-alert v-if="error" class="mt-3" variant="danger" show>{{ error }}</b-alert>

      <data-table
        v-if="itemsSize"
        class="mt-1"
        :items="items"
        :columns="fields"
        :selectedFields="selectedFields"
      />
    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import DefaultLayout from '@/layouts/DefaultLayout';
import DataTable from '@/components/DataTable';
import Preloader from '@/components/Preloader';

export default {
  name: 'ItemsPage',

  components: {
    DefaultLayout,
    DataTable,
    Preloader,
  },

  data() {
    return {
      customQuery: '',
      error: '',
    };
  },

  computed: {
    ...mapState({
      db: state => state.settings.database,
      items: state => state.items.queryItems,
      isLoading: state => state.items.isLoading,
    }),

    ...mapGetters({
      fields: 'items/queryFields',
    }),

    selectedFields() {
      return _.cloneDeep(this.fields);
    },

    itemsSize() {
      return _.size(this.items);
    },
  },

  methods: {
    onCustomQueryExecute() {
      const upCasedQuery = this.customQuery.toUpperCase();
      if (upCasedQuery.includes('SELECT') && !upCasedQuery.includes('LIMIT')) {
        this.error = 'Expect LIMIT expression for SELECT query';

        return;
      }

      const query = this.customQuery.trim();

      const pload = {
        db: this.db,
        query,
      };

      this.$store.dispatch('items/getItemsByQuery', pload);
    },

    changeQuery() {
      this.error = '';
    },
  },
};
</script>

<style scoped>
.datatable {
  margin-top: 30px;
}
</style>



