<template>
  <div class="datatable-container">
    <b-row>
      <b-col cols="6">
        <b-dropdown v-if="isCreateAllowed" class="float-left mr-2" variant="primary" size="md" no-caret left @shown="onCreateAreaShown">
            <template slot="button-content">
              <octicon class="mt-1" name="plus" scale="1" />
            </template>
            <editor class="editor m-2" v-model="newItemJson" lang="json" theme="chrome" width="810px" height="450px" />
            <div class="text-center">
                <b-button variant="success" size="md" class="mt-2 mb-2" @click.stop="onCreate(newItemJson)">Create</b-button>
            </div>
        </b-dropdown>
        <h4 class="float-left">{{ title }}</h4>
      </b-col>
      <b-col cols="6">
        <b-dropdown variant="primary" class="pull-right" text="Columns">
          <a
            v-for="column, index in columns"
            class="dropdown-item datatable_custom_dropdown_item"
            replace
            :key="index"
            v-on:click.stop="onFieldSelect(column)">
            <octicon v-if="checkIsFieldSelected(column)" name="check" />{{column}}
          </a>
        </b-dropdown>
      </b-col>
    </b-row>

    <b-table striped bordered hover fixed small no-local-sorting
      v-if="tableFieldsSelectedNotEmpty"
      :items="tableItems"
      :fields="fields"
      @sort-changed="onSortChange"
      class="mt-2"
    >

    <template v-for="field in fields" :slot="field.key" slot-scope="data">
      <div class="datatable_item_wrapper">
        {{ data.value }}
      </div>
    </template>

      <template slot="_actions" slot-scope="row">
        <b-button :variant='row.detailsShowing ? "primary" : ""' size="sm" @click.stop="row.toggleDetails" class="mr-2"><octicon name="pencil" /></b-button>
        <b-button v-if="isRemoveAllowed" variant="danger" size="sm" @click.stop="onDelete(row.index)" class="mr-2"><octicon name="trashcan" /></b-button>
      </template>

      <template slot="row-details" slot-scope="row">
        <b-card>
          <editor class="editor mt-1" @input="(str) => { applyEditorChanges(row.index, str) }" :value="prettyStringifyObject(mutableItems[row.index])" lang="json" theme="chrome" width="100%" height="400px" />
          <div v-if="isUpdateAllowed" class="text-center">
              <b-button variant="primary" size="md" class="mt-2" @click.stop="onUpdate(row.index)">Update</b-button>
          </div>
        </b-card>
      </template>
    </b-table>

    <b-row class="mt-2">
      <b-col cols="4">
        <h6 v-if="isTotalShowed"><strong>Total: {{totalItems || itemsSize}}</strong></h6>
      </b-col>
      <b-col cols="4"  class="text-center">
        <b-dropdown v-if="isPaginable" variant="primary" :text="pageSizeString">
          <b-dropdown-item
            v-for="pageSize in paginatorLimitOptions"
            v-bind:key="pageSize"
            @click="onPageSizeChange(pageSize)">{{pageSize}}
          </b-dropdown-item>
        </b-dropdown>
      </b-col>
      <b-col cols="4">
        <b-pagination v-if="isPaginable" class="float-right" size="md" hide-ellipsis :limit="paginatorPagesMax" :total-rows="totalItems"
            :per-page="itemsPerPage" v-model="tablePage" v-on:input="onPageChange">
        </b-pagination>
      </b-col>
    </b-row>

  </div>
</template>

<script>
import _ from 'lodash';
import Octicon from 'vue-octicon/components/Octicon';
import Editor from 'vue2-ace-editor';

export default {
  name: 'DataTable',

  components: {
    Octicon,
    Editor,
  },

  // TODO use emit instead of prop funtions
  // TODO use mixins to improve code readability
  props: {
    title: {
      type: String,
      default: '',
    },

    items: {
      type: Array,
      default: () => [],
    },

    itemTemplate: {
      type: String,
      default: '',
    },

    totalItems: {
      type: Number,
      default: 0,
    },

    columns: {
      type: Array,
      default: () => [],
    },

    paginatorLimitOptions: {
      type: Array,
      default: () => [10, 25, 50, 100],
      validator: arr => !arr.some(isNaN),
    },

    paginatorPagesMax: {
      type: Number,
      default: 5,
    },

    paginatorItemsPerPage: {
      type: Number,
      default: this.itemsSize,
    },

    page: {
      type: Number,
      default: 1,
    },

    pageSize: {
      type: Number,
      default: 10,
    },

    selectedFields: {
      type: Array,
      default: () => [],
    },

    sortableFields: {
      type: Array,
      default: () => [],
    },

    isWithActions: {
      type: Boolean,
      default: false,
    },

    isUpdateAllowed: {
      type: Boolean,
      default: false,
    },

    isRemoveAllowed: {
      type: Boolean,
      default: false,
    },

    isCreateAllowed: {
      type: Boolean,
      default: false,
    },

    isPaginable: {
      type: Boolean,
      default: false,
    },

    isTotalShowed: {
      type: Boolean,
      default: false,
    },

    sortField: {
      type: String,
      default: '',
    },

    sortDesc: {
      type: Boolean,
      default: false,
    },

    onSelectedFieldsChange: {
      type: Function,
      default: _.noop,
    },

    onQuery: {
      type: Function,
      default: _.noop,
    },

    onItemDelete: {
      type: Function,
      default: _.noop,
    },

    onItemUpdate: {
      type: Function,
      default: _.noop,
    },

    onItemCreate: {
      type: Function,
      default: _.noop,
    },
  },

  data() {
    return {
      tableItems: _.cloneDeep(this.items),
      mutableItems: _.cloneDeep(this.items),
      tableSelectedFields: _.cloneDeep(this.selectedFields),
      tablePage: this.page,
      tablePageSize: this.pageSize,
      tableSortField: this.sortField,
      tableSortDesc: this.sortDesc,
      newItemJson: this.itemTemplate,
    };
  },

  beforeUpdate() {
    this.tablePage = this.page;
    this.tablePageSize = this.pageSize;
    this.tableSortField = this.sortField;
    this.tableSortDesc = this.sortDesc;
    this.tableSelectedFields = this.selectedFields;
    this.newItemJson = this.itemTemplate;
  },

  computed: {
    fields() {
      return this.getFields();
    },

    pageSizeString() {
      return this.pageSize.toString();
    },

    itemsSize() {
      return _.size(this.items);
    },

    tableFieldsSelectedNotEmpty() {
      return !_.isEmpty(this.tableSelectedFields);
    },

    itemsPerPage() {
      return this.isPaginable ? this.pageSize : this.paginatorItemsPerPage;
    },
  },

  methods: {
    getFields() {
      if (_.isEmpty(this.items)) {
        return [];
      }

      const fieldsObj = this.columns.reduce((fieldsTable, current) => {
        if (!this.tableSelectedFields.includes(current)) {
          return fieldsTable;
        }

        const fieldsTableObj = fieldsTable;

        fieldsTableObj[current] = {
          key: current,
          label: current,
          sortable: !_.isEmpty(this.sortableFields) && this.sortableFields.includes(current),
          class: 'datatable_column',
        };

        return fieldsTableObj;
      }, {});

      if (this.isWithActions) {
        fieldsObj.show_details = {
          key: '_actions',
          label: 'Actions',
          class: 'datatable_actions_column',
        };
      }

      return fieldsObj;
    },

    getParams() {
      const { tablePage, tablePageSize, tableSortField, tableSortDesc } = this;

      const params = {
        page: tablePage,
        pageSize: tablePageSize,
      };

      if (tableSortField) {
        return {
          ...params,
          sortField: tableSortField,
          sortDesc: tableSortDesc,
        };
      }

      return params;
    },

    onPageChange(page) {
      this.tablePage = page;
      const params = this.getParams();

      this.onQuery(params);
    },

    onPageSizeChange(pageSize) {
      this.tablePage = 1;
      this.tablePageSize = pageSize;
      const params = this.getParams();

      this.onQuery(params);
    },

    onSortChange(ctx) {
      const { sortBy, sortDesc } = ctx;

      this.tableSortField = sortBy;
      this.tableSortDesc = sortDesc;

      const params = this.getParams();

      this.onQuery(params);
    },

    onDelete(index) {
      this.onItemDelete(this.items[index]);
    },

    onUpdate(itemIndex) {
      this.onItemUpdate(this.mutableItems[itemIndex]);
    },

    onCreate(itemJson) {
      const item = JSON.parse(itemJson);
      this.onItemCreate(item);
    },

    onFieldSelect(field) {
      if (this.tableSelectedFields.includes(field)) {
        const fieldIndex = this.tableSelectedFields.indexOf(field);
        this.tableSelectedFields.splice(fieldIndex, 1);
      } else {
        this.tableSelectedFields.push(field);
      }

      this.onSelectedFieldsChange(this.tableSelectedFields);
    },

    checkIsFieldSelected(field) {
      return this.tableSelectedFields.includes(field);
    },

    prettyStringifyObject(obj) {
      return JSON.stringify(obj, null, '  ');
    },

    applyEditorChanges(index, str) {
      const obj = JSON.parse(str);
      this.mutableItems[index] = obj;
    },
  },

  watch: {
    items(newItems) {
      this.tableItems = _.cloneDeep(newItems);
      this.mutableItems = _.cloneDeep(newItems);
    },

    itemTemplate(newItemTemplate) {
      this.newItemJson = newItemTemplate;
    },
  },
};
</script>

<style>
.datatable-container {
  min-height: 650px;
}

.datatable_column {
  overflow: hidden;
}

.datatable_item_wrapper {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.datatable_actions_column {
  width: 100px;
}

.datatable_columns_scroll_container {
  min-height: 220px;
}

.datatable_custom_dropdown_item {
  cursor: pointer;
  user-select: none;
}

.editor {
  font-size: 18px;
  margin: 0 auto;
  border: 1px gray solid;
}
</style>



