<template>
  <default-layout>
    <div slot="page">
      <div class="container mt-5">
      <b-jumbotron header="Reindexer Face" lead="A tool that provides the most convenient way of your data management" >
        <p>Just try and enjoy!</p>
        <b-btn variant="primary" href="#" :to="webroutes.items">Get started!</b-btn>
      </b-jumbotron>
      </div>
    </div>
  </default-layout>
</template>

<script>
import DefaultLayout from '@/layouts/DefaultLayout';
import webroutes from '@/router/webroutes';

export default {
  name: 'MainPage',

  components: {
    DefaultLayout,
  },

  data() {
    return {
      webroutes,
    };
  },
};
</script>



