<template>
  <div class="header">
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark header-panel fixed-top">
      <b-navbar-brand class="header-brand" href="#" :to="webroutes.root"><img src="../assets/reindexer-logo.png">Reindexer Face</b-navbar-brand>

      <b-navbar-nav class="header-menu">
        <b-nav-item href="#" :to="webroutes.items" exact>Items</b-nav-item>
        <b-nav-item href="#" :to="webroutes.namespaces" exact>Namespace</b-nav-item>
        <b-nav-item href="#" :to="webroutes.queries" exact>Query</b-nav-item>
      </b-navbar-nav>

      <b-navbar-nav class="ml-auto">
        <b-button-group>
          <b-dropdown class="header-database-select-button" variant="dark" :text="databaseSelected || database">
            <b-dropdown-header>Databases</b-dropdown-header>
            <b-dropdown-item-button
              v-for="database, index in databases"
              :key="index"
              @click="onDatabaseSelect(database)">{{database}}</b-dropdown-item-button>
          </b-dropdown>

          <b-dropdown class="header-database-create-button" variant="dark" size="md" no-caret>
              <template slot="button-content">
                <octicon class="mt-1" name="plus" scale="1" />
              </template>
              <b-container class="header-database-dropdown-menu mt-2 mb-3">
                <b-row>
                  <b-col cols="8">
                    <b-form-input v-model="newDatabase" variant="primary" size="sm" type="text" placeholder="New database" />
                  </b-col>
                  <b-col cols="2">
                    <b-button
                      size="sm"
                      variant="success"
                      :disabled="isNewDatabaseEmpty"
                      @click="onDatabaseCreate(newDatabase)">Create</b-button>
                  </b-col>
                </b-row>
              </b-container>
          </b-dropdown>

          <b-button size="sm" variant="dark" @click="onDatabaseDeleteQuestion(database)" :disabled="!isDatabaseSelected">
            <octicon class="mt-1" name="trashcan" scale="1" />
          </b-button>
        </b-button-group>

      </b-navbar-nav>
    </nav>
  </div>
</template>

<script>
import _ from 'lodash';
import webroutes from '@/router/webroutes';
import Octicon from 'vue-octicon/components/Octicon';

export default {
  name: 'NavHeader',

  components: {
    Octicon,
  },

  props: {
    databaseSelected: {
      type: String,
      default: '',
    },

    databases: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      webroutes,
      newDatabase: '',
    };
  },

  computed: {
    database() {
      if (!this.databaseSelected && !_.isEmpty(this.databases)) {
        const newDatabaseSelected = this.databases[0];
        this.$emit('onDatabaseSelect', newDatabaseSelected);

        return newDatabaseSelected;
      }

      return this.databaseSelected;
    },

    isNewDatabaseEmpty() {
      return _.isEmpty(this.newDatabase);
    },

    isDatabaseSelected() {
      return !_.isEmpty(this.databaseSelected);
    },
  },

  mounted() {
    this.$emit('onDataLoad');
  },

  // TODO Refactoring: get methods and props from DefaultLayout
  methods: {
    onDatabaseSelect(database) {
      if (this.databaseSelected !== database) {
        this.$emit('onDatabaseSelect', database);
      }
    },

    onDatabaseCreate(database) {
      this.$store.dispatch('dbs/createDb', database);
    },

    onDatabaseDeleteQuestion(database) {
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the database:',
        strongMessage: database,
        event: 'onDatabaseDelete',
        eventParams: database,
      });
    },
  },

  watch: {
    databases(newDatabases) {
      if (!newDatabases.includes(this.database)) {
        const newDatabaseSelected = !_.isEmpty(this.databases[0]) ? this.databases[0] : '';
        this.$emit('onDatabaseSelect', newDatabaseSelected);
      }
    },
  },
};
</script>

<style scoped>
.header {
  width: 100%;
}

.header-panel {
  height: 48px;
  border-bottom: 1px solid #212529;
  box-shadow: 0px 1px 1px #32383e;
}

.header-brand {
  margin-left: 20px;
}

.header-menu {
  margin-left: 30px;
}

.header-database-dropdown-menu {
  width: 300px;
}
</style>



