import _ from 'lodash';
import resultGetter from '@/utils/resultGetter';

export default {
  names: state => _.map(state.list, item => item.name),
  icons: state => _.map(state.list, item => (item.storage_enabled ? 'database' : 'flame')),
  indexes: state => _.get(state, 'one.indexes', []),
  pk: (state, getters) => _.filter(getters.indexes, { is_pk: true }),
  notArrayIndexes: (state, getters) => _.filter(getters.indexes, { is_array: false }),
  sortableFields: (state, getters) => getters.notArrayIndexes.map(index => index.name),
  indexesNames: (state, getters) => getters.indexes.map(index => index.name),
  indexFields: (state, getters) => resultGetter.fields(getters.indexes),
  itemTemplate: (state, getters) => {
    const { indexes } = getters;

    const props = indexes.reduce((accumulator, indexData, indexNum) => {
      const { name } = indexData;
      const fieldType = indexData.field_type;

      if (fieldType === 'composite') {
        return accumulator;
      }

      let value;
      switch (fieldType) {
        case 'string':
          value = '""';
          break;
        case 'bool':
          value = true;
          break;
        case 'double':
          value = 0.1;
          break;
        case 'int':
        case 'int64':
          value = 0;
          break;
        default:
          value = null;
          break;
      }

      return indexNum > 0 ? `${accumulator},\n"${name}": ${value}` : `\n"${name}": ${value}`;
    }, '');

    return JSON.stringify(JSON.parse(`{${props}}`), null, 2);
  },
};



