import Api from '@/api';
import routes, { routeConstructor } from '@/api/routes';
import resultGetter from '@/utils/resultGetter';
import types from './mutation-types';

export const reset = (context) => {
  const { commit } = context;

  commit(types.NAMESPACES_RESET);
};

export const getNamespacesList = (context, db) => {
  const { state, commit, dispatch } = context;
  const params = {
    ...state.params,
    sortOrder: 'asc',
  };

  commit(types.NAMESPACES_GET_LIST.REQUEST);

  Api.get(routeConstructor(routes.namespaces, db), params)
    .then(
      (response) => {
        const { data } = response;
        const payload = {
          list: resultGetter.items(data),
        };

        commit(types.NAMESPACES_GET_LIST.SUCCESS, payload);
      },
      (error) => {
        commit(types.NAMESPACES_GET_LIST.FAIL, error);

        return dispatch('settings/throwError', error, { root: true });
      },
    )
    .catch((error) => {
      commit(types.NAMESPACES_GET_LIST.FAIL, error);

      return dispatch('settings/throwError', error, { root: true });
    });
};

export const getNamespaceOne = (context, pload) => {
  const { commit, dispatch } = context;

  const { db, namespace } = pload;

  commit(types.NAMESPACES_GET_ONE.REQUEST);
  Api.get(routeConstructor(routes.namespace, db, namespace))
    .then(
      (response) => {
        const { data } = response;
        const payload = {
          one: data,
        };

        commit(types.NAMESPACES_GET_ONE.SUCCESS, payload);
      },
      (error) => {
        commit(types.NAMESPACES_GET_ONE.FAIL, error);

        return dispatch('settings/throwError', error, { root: true });
      },
    )
    .catch((error) => {
      commit(types.NAMESPACES_GET_ONE.FAIL, error);

      return dispatch('settings/throwError', error, { root: true });
    });
};

export const setNamespacesParams = (context, params = {}) => {
  const { commit } = context;
  const payload = {
    params,
  };

  commit(types.NAMESPACES_PARAMS_SET, payload);
};

export const createNamespace = (context, pload) => {
  const { commit, dispatch } = context;
  const { db, json } = pload;

  commit(types.NAMESPACE_CREATE.REQUEST);

  Api.create(routeConstructor(routes.namespaces, db), json)
    .then(
      () => {
        commit(types.NAMESPACE_CREATE.SUCCESS);
        dispatch('settings/showSuccessMessage', 'Namespace is created', { root: true });

        return dispatch('getNamespacesList', db);
      },
      (error) => {
        commit(types.NAMESPACE_CREATE.FAIL, error);

        return dispatch('settings/throwError', error, { root: true });
      },
    )
    .catch((error) => {
      commit(types.NAMESPACE_CREATE.FAIL, error);

      return dispatch('settings/throwError', error, { root: true });
    });
};

export const deleteNamespace = (context, pload) => {
  const { commit, dispatch } = context;
  const { db, namespace } = pload;

  commit(types.NAMESPACE_DELETE.REQUEST);

  Api.delete(routeConstructor(routes.namespace, db, namespace))
    .then(
      () => {
        commit(types.NAMESPACE_DELETE.SUCCESS);
        dispatch('settings/showSuccessMessage', 'Namespace is deleted', {
          root: true,
        });

        return dispatch('getNamespacesList', db);
      },
      (error) => {
        commit(types.NAMESPACE_DELETE.FAIL, error);

        return dispatch('settings/throwError', error, { root: true });
      },
    )
    .catch((error) => {
      commit(types.NAMESPACE_DELETE.FAIL, error);

      return dispatch('settings/throwError', error, { root: true });
    });
};

export const deleteIndex = (context, pload) => {
  const { commit, dispatch } = context;
  const { db, namespace, index } = pload;

  commit(types.NAMESPACE_INDEX_DELETE.REQUEST);

  Api.delete(routeConstructor(routes.index, db, namespace, index))
    .then(
      () => {
        commit(types.NAMESPACE_INDEX_DELETE.SUCCESS);
        dispatch('settings/showSuccessMessage', 'Namespace is deleted', {
          root: true,
        });

        return dispatch('getNamespacesList', db);
      },
      (error) => {
        commit(types.NAMESPACE_INDEX_DELETE.FAIL, error);

        return dispatch('settings/throwError', error, { root: true });
      },
    )
    .catch((error) => {
      commit(types.NAMESPACE_INDEX_DELETE.FAIL, error);

      return dispatch('settings/throwError', error, { root: true });
    });
};

export const createIndex = (context, pload) => {
  const { commit, dispatch } = context;
  const { db, namespace, index } = pload;

  commit(types.NAMESPACE_INDEX_CREATE.REQUEST);

  Api.create(routeConstructor(routes.indexes, db, namespace), index)
    .then(
      () => {
        commit(types.NAMESPACE_INDEX_CREATE.SUCCESS);
        dispatch('settings/showSuccessMessage', 'Namespace is deleted', {
          root: true,
        });

        return dispatch('getNamespacesList', db);
      },
      (error) => {
        commit(types.NAMESPACE_INDEX_CREATE.FAIL, error);

        return dispatch('settings/throwError', error, { root: true });
      },
    )
    .catch((error) => {
      commit(types.NAMESPACE_INDEX_CREATE.FAIL, error);

      return dispatch('settings/throwError', error, { root: true });
    });
};

export default {
  reset,
  getNamespacesList,
  getNamespaceOne,
  setNamespacesParams,
  createNamespace,
  deleteNamespace,
  deleteIndex,
  createIndex,
};



