<template>
  <div>
    <nav-header
      :databases='databases'
      :databaseSelected='database'
      @onDataLoad="fetchDatabases"
      @onDatabaseSelect="onDatabaseSelect"
    />
    <modal @onDatabaseDelete="onDatabaseDelete" @onNamespaceDelete="onNamespaceDelete" @onNamespaceCreate="onNamespaceCreate"
      @onItemDelete="onItemDelete" @onItemCreate="onItemCreate" @onItemUpdate="onItemUpdate"
      @onIndexDelete="onIndexDelete" @onIndexCreate="onIndexCreate" />
    <div class="content-container" v-bind:class="{ 'content-container-min': isSidebarOpened }">
      <sidebar
        :items='sidebarItems'
        :iconsSet='sidebarIcons'
        :selectedNamespace='namespace'
        :isOpened='isSidebarOpened'
        :isItemsLoading='sidebarIsLoading'
        :isSelectedLoading='sidebarIsSelectedLoading'
        :isDisabled='sidebarIsDisabled'
        @onToggle="onSidebarToggle"
        @onDataLoad="onSidebarDataLoad"
        @onNamespaceSelect="onNamespaceSelect"
        @onNamespaceDelete="onNamespaceDeleteQuestion"
        @onNamespaceCreate="onNamespaceCreateQuestion"
      />
      <div class="page-content">
        <slot name="page" />
      </div>
      <toast position="se" />
    </div>
  </div>
</template>

<script>
import _ from 'lodash';
import Octicon from 'vue-octicon/components/Octicon';
import { mapState, mapActions } from 'vuex';
import { Toast } from 'vuex-toast';
import Modal from '@/components/Modal';
import NavHeader from '@/components/NavHeader';
import Sidebar from '@/components/SideBar';

export default {
  name: 'default-layout',

  components: {
    Modal,
    Octicon,
    Sidebar,
    Toast,
    NavHeader,
  },

  computed: {
    ...mapState({
      isSidebarOpened: state => state.settings.isSidebarOpened,
      sidebarItems: state => state.namespaces.list.map(item => item.name),
      sidebarIcons: state => state.namespaces.list.map(item => item.storage_enabled ? 'database' : 'flame'),
      databases: state => state.dbs.list,
      database: state => state.settings.database,
      namespace: state => state.settings.namespace,
      sidebarIsLoading: state => state.namespaces.isLoading,
      sidebarIsSelectedLoading: state => state.items.isLoading,
      sidebarIsDisabled: state => _.isEmpty(state.settings.database),
    }),
  },

  methods: {
    ...mapActions({
      selectDatabase: 'settings/selectDatabase',
      fetchDatabases: 'dbs/getDbList',
      fetchNamespaces: 'namespaces/getNamespacesList',
      onSidebarSelectItem: 'settings/selectNamespace',
      onSidebarToggle: 'settings/sidebarToggle',
      resetNamespaces: 'namespaces/reset',
      resetItems: 'items/reset',
      resetSelectedNamespace: 'settings/resetSelectedNamespace',
    }),

    onSidebarDataLoad() {
      if (this.database) {
        this.fetchNamespaces(this.database);
      }
    },

    onDatabaseSelect(selectedDb) {
      this.resetNamespaces();
      this.resetItems();
      this.resetSelectedNamespace();
      this.selectDatabase(selectedDb).then(() => this.fetchNamespaces(this.database));
    },

    onNamespaceSelect(selectedItem) {
      const pload = {
        db: this.database,
        namespace: selectedItem,
      };

      this.onSidebarSelectItem(selectedItem);
      this.$store.dispatch('items/setItemsParams', {}).then(() => {
        this.$store.dispatch('items/getItemsList', pload);
      });
      this.$store.dispatch('namespaces/getNamespaceOne', pload);
    },

    onNamespaceDeleteQuestion(namespace) {
      const payload = {
        db: this.database,
        namespace,
      };

      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the namespace:',
        strongMessage: namespace,
        event: 'onNamespaceDelete',
        eventParams: payload,
      });
    },

    onNamespaceCreateQuestion(namespace) {
      const payload = {
        db: this.database,
      };

      this.$modal.show({
        type: 'creator',
        header: 'Create',
        message: 'This action will create a namespace:',
        strongMessage: namespace,
        event: 'onNamespaceCreate',
        eventParams: payload,
      });
    },

    onDatabaseDelete(db) {
      this.$store.dispatch('dbs/deleteDb', db);
    },

    onNamespaceCreate(data) {
      const { db, value } = data;

      const json = {
        name: value,
        storage: {
          enabled: true,
          drop_on_file_format_error: true,
          create_if_missing: true,
        },
        indexes: [],
      };

      const pload = {
        db,
        json,
      };

      this.$store.dispatch('namespaces/createNamespace', pload);
    },

    onNamespaceDelete(payload) {
      this.$store.dispatch('namespaces/deleteNamespace', payload);
    },

    onItemDelete(item) {
      const pload = {
        db: this.database,
        namespace: this.namespace,
        item,
      };

      this.$store.dispatch('items/deleteItem', pload);
    },

    onItemUpdate(item) {
      const pload = {
        db: this.database,
        namespace: this.namespace,
        item,
      };

      this.$store.dispatch('items/updateItem', pload);
    },

    onItemCreate(item) {
      const pload = {
        db: this.database,
        namespace: this.namespace,
        item,
      };

      this.$store.dispatch('items/createItem', pload);
    },

    onIndexDelete(indexName) {
      const pload = {
        db: this.database,
        namespace: this.namespace,
        index: indexName,
      };

      this.$store.dispatch('namespaces/deleteIndex', pload);
    },

    onIndexCreate(index) {
      const pload = {
        db: this.database,
        namespace: this.namespace,
        index,
      };

      this.$store.dispatch('namespaces/createIndex', pload);
    },
  },

  watch: {
    database(newDatabase) {
      this.fetchNamespaces(newDatabase);
    },
  },
};
</script>

<style scoped>
.content-container {
  margin-left: 50px;
  overflow-x: hidden;
  transition: width 0.3s ease-in-out;
}

.content-container-min {
  margin-left: 260px;
  transition: width 0.3s ease-in-out;
}

.page-content {
  margin: 70px 40px 20px 40px;
}
</style>



