import ModalComponent from './Modal';
import ModalEventBus from './ModalEventBus';

const Modal = {
  install(Vue = {}, options = {}) {
    const ModalPlugin = {
      $vm: null,
      state: {},
      data: {
        ...options,
      },

      init(vm) {
        this.$vm = vm;
      },

      show(opts) {
        if (!this.$vm) return;

        ModalEventBus.$emit('show', opts);
      },
    };

    ModalPlugin.init(ModalEventBus);

    Vue.component('Modal', ModalComponent);

    // eslint-disable-next-line
    Vue.prototype.$modal = ModalPlugin;
  },
};

export default Modal;



