<template>
  <div class="sidebar-container bg-dark" v-bind:class="{ 'sidebar-container-max': isOpened }">
    <button class="btn-block sidebar-toggler text-muted" v-on:click="onToggle">
      <octicon class="sidebar-icon" name="grabber" scale="3" />
    </button>

    <div class="sidebar-control-panel" v-if="isOpened">
      <button class="sidebar-panel-button sidebar-panel-button-left text-white float-left"
         v-bind:class="{ 'sidebar-panel-button-disabled': isDisabled || !isNamespaceSelected }"
         @click="onNamespaceDelete(selectedNamespace)">
        <octicon name="trashcan" scale="2" />
      </button>

      <button class="sidebar-panel-button sidebar-panel-button-left text-white"
         v-bind:class="{ 'sidebar-panel-button-disabled': isDisabled }"
         @click="onNamespaceCreate">
        <octicon name="plus" scale="2" />
      </button>

      <button class="sidebar-panel-button sidebar-panel-button-right text-white float-right"
        v-bind:class="{ 'sidebar-panel-button-disabled': isDisabled }" @click="onDataLoad">
        <octicon name="sync" scale="2" :spin="isItemsLoading" />
      </button>
    </div>

    <div class="sidebar-items-list" v-bind:class="{ 'sidebar-hide': !isOpened }">
      <scrolly class="sidebar-scroll-container">
        <scrolly-viewport>
          <ul class="list-unstyled">
            <li v-for="(item, index) in items" class="item" v-on:mouseover="mouseOver" v-bind:class="{ 'item-active': selectedNamespace === item }">
              <a class="link" v-on:click="onNamespaceSelect(item)">
                <!-- <octicon class="icon" :name="itemIconSelect(index)" scale="1" :spin="isSpinnable(item)" /> -->
                <span class="text item-text">{{item}}</span>
              </a>
            </li>
          </ul>
        </scrolly-viewport>
        <scrolly-bar axis="y"></scrolly-bar>
      </scrolly>
    </div>
  </div>


</template>

<script>
import _ from 'lodash';
import { Scrolly, ScrollyViewport, ScrollyBar } from 'vue-scrolly';
import Octicon from 'vue-octicon/components/Octicon';

export default {
  name: 'SideBar',

  components: {
    Scrolly,
    ScrollyViewport,
    ScrollyBar,
    Octicon,
  },

  data() {
    return {
      newNamespace: '',
    };
  },

  props: {
    items: {
      type: Array,
      default: () => [],
    },

    iconsSet: {
      type: Array,
      default: () => [],
    },

    selectedNamespace: {
      type: String,
      default: '',
    },

    isOpened: {
      type: Boolean,
      default: false,
    },

    isItemsLoading: {
      type: Boolean,
      default: false,
    },

    isSelectedLoading: {
      type: Boolean,
      default: false,
    },

    isDisabled: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    isNamespaceSelected() {
      return _.size(this.selectedNamespace);
    },

    isNewNamespaceEmpty() {
      return _.isEmpty(this.newNamespace);
    },
  },

  mounted() {
    this.onDataLoad();
  },

  methods: {
    isSpinnable(item) {
      return item === this.selectedNamespace && this.isSelectedLoading;
    },

    itemIconSelect(iconNum) {
      return this.iconsSet[iconNum] ? this.iconsSet[iconNum] : '';
    },

    mouseOver() {
      this.active = !this.active;
    },

    onToggle() {
      this.$emit('onToggle');
    },

    onDataLoad() {
      this.$emit('onDataLoad');
    },

    onNamespaceSelect(item) {
      this.$emit('onNamespaceSelect', item);
    },

    onNamespaceDelete(item) {
      this.$emit('onNamespaceDelete', item);
    },

    onNamespaceCreate() {
      this.$emit('onNamespaceCreate');
    },
  },
};
</script>

<style scoped>
.sidebar-container {
  top: 48px;
  left: 0;
  width: 48px;
  position: fixed;
  overflow: hidden;
  height: 100%;
}

.sidebar-container-max {
  width: 260px;
}

.sidebar-hide {
  display: none;
}

.sidebar-toggler {
  border: none;
  background-color: Transparent;
  cursor: pointer;
  outline: none;

  border-bottom: 1px solid #212529;
  box-shadow: 0px 1px 1px #32383e;
}

.sidebar-control-panel {
  height: 50px;
}

.sidebar-items-list {
  border-top: 1px solid #212529;
  box-shadow: 0px 1px 1px #32383e;
}

.sidebar-icon {
  margin-bottom: -10px;
}

.item {
  cursor: pointer;
}

.item:hover {
  background: #292929;
}

.item:active {
  background: #191919;
}

.item-active {
  background: #191919;
}

.item-text {
  text-overflow: ellipsis;
  margin-right: 5px;
}

.icon {
  border-radius: 8px;
  color: #fff;
  display: block;
  height: 33px;
  overflow: hidden;
  padding: 8px 0 0;
  text-align: center;
  transition: width 0;
  width: 40px;
}

.text {
  margin-left: 5px;
  overflow: hidden;
  padding: 9px 0 6px 10px;
  white-space: nowrap;
  color: white;
  font-size: 16px;
  color: white;
  user-select: none;
}

.link {
  display: flex;
  text-decoration: none;
  padding-bottom: 3px;
  padding-top: 3px;
}

.sidebar-scroll-container {
  min-height: 860px;
}

.sidebar-panel-button {
  background: none;
  cursor: pointer;
  border: none;
  outline: none;
  margin-top: 7px;
}

.sidebar-panel-button-left {
  margin-left: 10px;
}

.sidebar-panel-button-left {
  margin-left: 10px;
}

.sidebar-panel-button-right {
  margin-right: 10px;
}

.sidebar-panel-button-disabled {
  opacity: 0.1;
  pointer-events: none;
}
</style>



