import _ from 'lodash';
import types from './mutation-types';

export default {
  [types.SETTINGS_DATABASE_RESET](state) {
    state.database = '';
  },
  [types.SETTINGS_NAMESPACE_RESET](state) {
    state.namespace = '';
    state.namespacesSelectedFields = {};
  },
  [types.SET_DATABASE](state, payload) {
    const { name } = payload;

    state.database = name;
  },
  [types.SET_NAMESPACE](state, payload) {
    const { name } = payload;

    state.namespace = name;
  },
  [types.SIDEBAR_TOGGLE](state) {
    state.isSidebarOpened = !state.isSidebarOpened;
  },
  [types.TABLE_SET_NAMESPACES_COLUMNS](state, payload) {
    const { db, namespace, fields } = payload;

    if (_.isEmpty(state.namespacesSelectedFields[db])) {
      state.namespacesSelectedFields[db] = {};
    }

    state.namespacesSelectedFields[db][namespace] = fields;
  },
};



