import types from './mutation-types';

export default {
  [types.NAMESPACES_RESET](state) {
    state.list = [];
    state.one = [];
    state.params = {};
    state.isLoading = false;
  },

  [types.NAMESPACES_GET_LIST.REQUEST](state) {
    state.isLoading = true;
  },
  [types.NAMESPACES_GET_LIST.FAIL](state) {
    state.isLoading = false;
  },
  [types.NAMESPACES_GET_LIST.SUCCESS](state, payload) {
    state.list = payload.list;
    state.isLoading = false;
  },

  [types.NAMESPACES_GET_ONE.REQUEST](state) {
    state.isLoading = true;
  },
  [types.NAMESPACES_GET_ONE.FAIL](state) {
    state.isLoading = false;
  },
  [types.NAMESPACES_GET_ONE.SUCCESS](state, payload) {
    state.one = payload.one;
    state.isLoading = false;
  },

  [types.NAMESPACES_PARAMS_SET](state, payload) {
    const { params } = payload;

    state.params = params;
  },

  [types.NAMESPACE_DELETE.REQUEST](state) {
    state.isLoading = true;
  },
  [types.NAMESPACE_DELETE.FAIL](state) {
    state.isLoading = false;
  },
  [types.NAMESPACE_DELETE.SUCCESS](state) {
    state.isLoading = false;
  },

  [types.NAMESPACE_CREATE.REQUEST](state) {
    state.isLoading = true;
  },
  [types.NAMESPACE_CREATE.FAIL](state) {
    state.isLoading = false;
  },
  [types.NAMESPACE_CREATE.SUCCESS](state) {
    state.isLoading = false;
  },

  [types.NAMESPACE_INDEX_DELETE.REQUEST](state) {
    state.isLoading = true;
  },
  [types.NAMESPACE_INDEX_DELETE.FAIL](state) {
    state.isLoading = false;
  },
  [types.NAMESPACE_INDEX_DELETE.SUCCESS](state) {
    state.isLoading = false;
  },

  [types.NAMESPACE_INDEX_CREATE.REQUEST](state) {
    state.isLoading = true;
  },
  [types.NAMESPACE_INDEX_CREATE.FAIL](state) {
    state.isLoading = false;
  },
  [types.NAMESPACE_INDEX_CREATE.SUCCESS](state) {
    state.isLoading = false;
  },
};



