<template>
  <default-layout>
    <div slot="page">
      <preloader :isLoading="isLoading" />
      <b-alert show v-if="!namespace">Select a namespace</b-alert>
      <data-table
        v-if="namespace"
        class="mt-1"
        isWithActions
        isUpdateAllowed
        isRemoveAllowed
        isCreateAllowed
        isPaginable
        isTotalShowed
        :title="namespace"
        :items="items"
        :itemTemplate="itemTemplate"
        :totalItems="totalItems"
        :columns="columns"
        :selectedFields="columnsFromSettings"
        :sortableFields="sortableFields"
        :page="page"
        :pageSize="pageSize"
        :sortField="sortField"
        :sortDesc="sortDesc"
        :onSelectedFieldsChange="onSelectedFieldsChange"
        :onQuery="onQuery"
        :onItemDelete="onItemDeleteQuestion"
        :onItemUpdate="onItemUpdateQuestion"
        :onItemCreate="onItemCreateQuestion"
      />
    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import Octicon from 'vue-octicon/components/Octicon';
import DefaultLayout from '@/layouts/DefaultLayout';
import Modal from '@/components/Modal';
import Preloader from '@/components/Preloader';
import DataTable from '@/components/DataTable';

export default {
  name: 'ItemsPage',

  components: {
    DefaultLayout,
    Modal,
    Preloader,
    DataTable,
    Octicon,
  },

  data() {
    return {
      newItemJson: '',
    };
  },

  computed: {
    ...mapState({
      db: state => state.settings.database,
      namespace: state => state.settings.namespace,
      columnsSelected: state => state.settings.namespacesSelectedFields,
      items: state => state.items.list,
      totalItems: state => state.items.total,
      isLoading: state => state.items.isLoading,
    }),

    ...mapGetters({
      indexesFields: 'namespaces/indexesNames',
      sortableFields: 'namespaces/sortableFields',
      itemTemplate: 'namespaces/itemTemplate',
      columns: 'items/fields',
      sortField: 'items/sortField',
      sortDesc: 'items/sortDesc',
      pageSize: 'items/pageSize',
      page: 'items/page',
    }),

    itemsSize() {
      return _.size(this.items);
    },

    columnsFromSettings() {
      const dbData = _.get(this.columnsSelected, this.db, null);

      if (!dbData) {
        return this.indexesFields;
      }

      return _.get(dbData, this.namespace, this.indexesFields);
    },
  },

  methods: {
    onSelectedFieldsChange(fields) {
      this.$store.dispatch('settings/setSelectedFieldsForNamespace', { db: this.db, namespace: this.namespace, fields });
    },

    onQuery(params) {
      const pload = {
        db: this.db,
        namespace: this.namespace,
      };
      this.$store.dispatch('items/setItemsParams', params).then(() => {
        this.$store.dispatch('items/getItemsList', pload);
      });
    },

    onItemDeleteQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the item:',
        strongMessage: itemPretty,
        event: 'onItemDelete',
        eventParams: itemPretty,
      });
    },

    onItemCreateQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will create a new item:',
        strongMessage: itemPretty,
        event: 'onItemCreate',
        eventParams: itemPretty,
      });
    },

    onItemUpdateQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will change the existed item:',
        strongMessage: itemPretty,
        event: 'onItemUpdate',
        eventParams: itemPretty,
      });
    },
  },
};
</script>

<style scoped>
.datatable {
  margin-top: 30px;
}

.editor {
  font-size: 20px;
  margin: 0 auto;
  border: 1px gray solid;
}
</style>



