import keyMirror from 'keymirror';
import createAsyncConstant from '@/utils/createAsyncConstant';

const asyncConstants = {
  NAMESPACES_GET_LIST: createAsyncConstant('getNamespacesList'),
  NAMESPACES_GET_ONE: createAsyncConstant('getNamespace'),
  NAMESPACE_DELETE: createAsyncConstant('deleteNamespace'),
  NAMESPACE_CREATE: createAsyncConstant('createNamespace'),
  NAMESPACE_INDEX_DELETE: createAsyncConstant('deleteIndex'),
  NAMESPACE_INDEX_CREATE: createAsyncConstant('createIndex'),
};

const constants = keyMirror({
  NAMESPACES_PARAMS_SET: null,
  NAMESPACES_RESET: null,
});

export default {
  ...asyncConstants,
  ...constants,
};



